import React from 'react'
import "../styles/cards.scss";
// import {ReactComponent as RightAero} from "../assets/arrow-right.svg"
// import {ReactComponent as LeftAero} from "../assets/arrow-left.svg"
export default function ProjectCards() {

    const data = [{   
        company:"Openspace",
        headline:"Open space comm the of its kind",
        img:"img (1).jpg"

    },
    {   
        company:"True sans",
        headline:"The only True sansa",
        img:"img (2).jpg"

    },{   
        company:"Dodge",
        headline:"Dodge any tax overwhelm",
        img:"img (3).jpg"

    }
    // {   
    //     company:"Openspace",
    //     headline:"Open space comm the of its kind",
    //     img:"img (1).jpg"

    // },
    // {   
    //     company:"Dodge",
    //     headline:"Dodge any tax overwhelm",
    //     img:"img (3).jpg"

    // },  {   
    //     company:"True sans",
    //     headline:"The only True sansa",
    //     img:"img (2).jpg"

    ]

    return (
        <section className="cards">
            {/* <div class="card-nav">
                <div className="card-nav-swipe"><LeftAero/></div>
                <div className="card-nav-swipe"><RightAero/></div>
            </div> */}
            <div className="cards-row" > 
            {
                data.map((card,i)=>    
                
                    <div className="cards-container" key={i}>
                        <div className="card-detail">
                            <span className="card-company">{card.company}</span>
                            <h2 className="card-headline">{card.headline}</h2>
                        </div>
                         <div className="card-img">
                         <img src={require(`../assets/${card.img}`)} alt={card.company} />
                         </div>
                        
                    </div>      
                
            )}
            </div>   
            
        </section>
    )
}
