import React,{useEffect} from 'react';
import './App.scss';
import Banner from './components/banner';
import Header from './components/header';
import ProjectCards from './components/projectCards';

import { gsap } from "gsap";
function App() {

  
  useEffect(() => {
    let vh = window.innerHeight*0.01
    document.documentElement.style.setProperty('--vh',`${vh}px`) 


    const t1 = gsap.timeline();

    t1.from('.line', {duration:1.8,
      y:100,
      ease:"power4.out",
      delay:1,
      opacity:0,
      stagger:{
        amount:0.3
      }
    }).to(".upper-black",{
      duration:1.6,
      height:0,
      ease:'expo.inOut',
      stagger:{
        amount:0.4
      }
    }).to(".lower-black",{
      duration:1, 
      width:0,
      ease:'expo.inOut',
      delay:-.8,
      stagger:{
        amount:0.4
      }
    })
    .from(".card-img img",{
      duration:1.6,
      scale:3.4,
      delay:-2,
      stagger:{
        amount:.04
      }
    })
  }, [])
  
  return (
    <div className="Main">
      <section className="wrapper">
        <div className='upper-black'></div>
        <div className='upper-black'></div>
        <div className='upper-black'></div>
        <Header/>
        <Banner/>
      </section>
      
      <section className="wrapper">
      <div className='lower-black'></div>
      <div className='lower-black'></div>
      <div className='lower-black'></div>
      <ProjectCards/>
      </section>
      
    </div>
  );
}

export default App;
